.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");

.container {
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.heading {
  font-size: 2.5rem;
  font-weight: bold;
}

.paragraph {
  font-size: 1.2rem;
  line-height: 1.5;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}

.regular{
  font-size: 1.2rem;
  line-height: 1.5;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}